import Sidebar from "@/components/sidebar";
import Image from "next/image";
import profileDp from "../public/uploads/profileDp.png";

export default function Downloads() {
  return (
    <>
       <Sidebar/>   
       <aside id="downloads" className=" leftSide fixed top-5 right-5 z-40 w-4/6 bg-white h-5/6 transition-transform -translate-x-full sm:translate-x-0" aria-label="downloads">
        <div className="download-wrapper">
           <aside className="lt absolute top-0 left-0 w-4/6 h-5/6 transition-transform -translate-x-full sm:translate-x-0">
               <div className="container max-auto">
                <p className="flex text-4xl text-start font-semibold px-4 py-4 hTitle">Daily Downloads
                  <span className="span-container rounded-full">
                    <Image src={profileDp} alt="" width={32} height={32}/>
                  </span>
                 
                </p>
               </div>
           </aside>
           <aside className="lt absolute top-0 right-0">

           </aside>
        </div>
       </aside>
    </>
  );
}
